import { getTimingFunction } from './patterns/timingFunctions';
import constants from "./constants";
export var retileGrid = function (grid, rows, cols) {
    var newGrid = [];
    for (var j = 0; j < rows; j++) {
        var newRow = [];
        var row = grid[j % grid.length];
        for (var i = 0; i < cols; i++) {
            newRow.push(row[i % row.length]);
        }
        newGrid.push(newRow);
    }
    return newGrid;
};
export var gridCopy = function (grid) { return grid.map(function (row) { return row.slice(); }); };
export var gridFlippedDiag = function (grid) {
    var rows = grid.length;
    var cols = grid[0].length;
    var newGrid = [];
    for (var j = 0; j < cols; j++) {
        var newRow = [];
        for (var i = 0; i < rows; i++) {
            newRow.push(grid[i][j]);
        }
        newGrid.push(newRow);
    }
    return newGrid;
};
export var getPatternsFromFrames = function (frames, g) {
    var patterns = [];
    var colors = constants.colors;
    frames.forEach(function (frame, n) {
        var lastGrid = n > 0 ? frames[n - 1].grid : undefined;
        var pattern = {
            getColor: function (col, row) {
                if (!frame.grid[row]) {
                    return colors[0];
                }
                var colorIdx = frame.grid[row][col];
                if (lastGrid && lastGrid[row][col] === colorIdx) {
                    return undefined;
                }
                else {
                    return colors[colorIdx];
                }
            },
            fade: frame.fade,
            getOffset: getTimingFunction(frame.timingFunc),
            wait: frame.wait,
        };
        patterns.push(pattern);
    });
    return patterns;
};
