var __assign = (this && this.__assign) || function () {
    __assign = Object.assign || function(t) {
        for (var s, i = 1, n = arguments.length; i < n; i++) {
            s = arguments[i];
            for (var p in s) if (Object.prototype.hasOwnProperty.call(s, p))
                t[p] = s[p];
        }
        return t;
    };
    return __assign.apply(this, arguments);
};
import { addEncodings } from "./encodings";
import { compressArrayArrayToDeltasFromLeft } from "./deltas";
import { gridFlippedDiag } from "../utils";
import { getTimingFunctionCodeSlug } from "../patterns/timingFunctions";
var convertFrameGridsToDeltas = function (frames) {
    var grid = frames[0].grid.map(function (row) { return row.map(function (r) { return -1; }); });
    var compressed = [];
    frames.forEach(function (frame) {
        var changes = [];
        frame.grid.forEach(function (row, j) {
            row.forEach(function (colorIdx, i) {
                if (grid[j][i] === colorIdx) {
                    return;
                }
                else {
                    grid[j][i] = colorIdx;
                    changes.push({ j: j, i: i, c: colorIdx });
                }
            });
        });
        compressed.push({
            type: 'deltas',
            deltas: changes,
            wait: frame.wait,
            fade: frame.fade,
            timingFunc: frame.timingFunc,
        });
    });
    return compressed;
};
var compressFrameWithGrid = function (frame) {
    var gridNormal = compressArrayArrayToDeltasFromLeft(frame.grid);
    var gridFlipped = compressArrayArrayToDeltasFromLeft(gridFlippedDiag(frame.grid));
    var flipped = !(JSON.stringify(gridNormal).length <= JSON.stringify(gridFlipped).length);
    return __assign({}, frame, { flipped: flipped, grid: flipped ? gridFlipped : gridNormal, type: 'cgrid' });
};
var compressFrameWithDeltas = function (frame) {
    var arrayedJFirst = frame.deltas.map(function (d) { return [d.j, d.i, d.c]; });
    var arrayedIFirst = frame.deltas.map(function (d) { return [d.i, d.j, d.c]; });
    var compJFirst = compressArrayArrayToDeltasFromLeft(arrayedJFirst);
    var compIFirst = compressArrayArrayToDeltasFromLeft(arrayedIFirst);
    var IFirst = (JSON.stringify(compIFirst).length <= JSON.stringify(compJFirst).length);
    return __assign({}, frame, { flipped: IFirst, type: 'cdeltas', deltas: IFirst ? compIFirst : compJFirst });
};
var slugifyFrames = function (frames) {
    var slugifyFrameWithDeltas = function (frame) {
        var f = frame.fade, w = frame.wait, d = frame.deltas, flipped = frame.flipped, timingFunc = frame.timingFunc;
        var timingFuncCode = getTimingFunctionCodeSlug(timingFunc);
        return flipped
            ? f + "," + w + timingFuncCode + "&" + d.map(function (delta) { return delta.join(','); }).join(':')
            : f + "," + w + timingFuncCode + "$" + d.map(function (delta) { return delta.join(','); }).join(':');
    };
    var slugifyFrameWithGrid = function (frame) {
        var f = frame.fade, w = frame.wait, g = frame.grid, flipped = frame.flipped, timingFunc = frame.timingFunc;
        var rowStrings = g.map(function (row) { return row.join(''); });
        var timingFuncCode = getTimingFunctionCodeSlug(timingFunc);
        return flipped
            ? f + "," + w + timingFuncCode + "<" + rowStrings.join(':')
            : f + "," + w + timingFuncCode + "^" + rowStrings.join(':');
    };
    return frames.map(function (frame) {
        if (frame.type === 'cdeltas') {
            return slugifyFrameWithDeltas(frame);
        }
        else if (frame.type === 'cgrid') {
            return slugifyFrameWithGrid(frame);
        }
        else
            return 'ERROR';
    });
};
var mergeCompressionResults = function (a, b) {
    var best = [];
    a.forEach(function (str, i) {
        if (str.length <= b[i].length) {
            best.push(str);
        }
        else {
            best.push(b[i]);
        }
    });
    return best;
};
var stats = function (x) { return x + " chars / " + (x / 1024).toFixed(2) + " KB"; };
var serializeFrames = function (frames) {
    var deltas = convertFrameGridsToDeltas(frames);
    var grids = frames;
    var deltasCompressed = deltas.map(compressFrameWithDeltas);
    var gridsCompressed = frames.map(compressFrameWithGrid);
    var deltaSlugs = slugifyFrames(deltasCompressed);
    var gridSlugs = slugifyFrames(gridsCompressed);
    var encDeltaSlugs = deltaSlugs.map(addEncodings);
    var encGridSlugs = gridSlugs.map(addEncodings);
    var best = mergeCompressionResults(encDeltaSlugs, encGridSlugs).join(';');
    var start = JSON.stringify(frames).length;
    var d0 = JSON.stringify(deltas).length;
    var d1 = JSON.stringify(deltasCompressed).length;
    var d2 = JSON.stringify(deltaSlugs).length;
    var d3 = JSON.stringify(encDeltaSlugs).length;
    var g0 = JSON.stringify(grids).length;
    var g1 = JSON.stringify(gridsCompressed).length;
    var g2 = JSON.stringify(gridSlugs).length;
    var g3 = JSON.stringify(encGridSlugs).length;
    console.log("uncompressed: " + stats(start));
    console.log('Deltas:');
    console.log("---deltified: " + stats(d0) + ", " + (100 * d0 / start).toFixed(2) + ' %');
    console.log("--compressed: " + stats(d1) + ", " + (100 * d1 / d0).toFixed(2) + ' %');
    console.log("-----slugged: " + stats(d2) + ", " + (100 * d2 / d1).toFixed(2) + ' %');
    console.log("-----encoded: " + stats(d3) + ", " + (100 * d3 / d2).toFixed(2) + ' %');
    console.log('ratio: ' + (100 * d3 / start).toFixed(2) + ' %');
    console.log('Grids:');
    console.log("--compressed: " + stats(g1) + ", " + (100 * g1 / g0).toFixed(2) + ' %');
    console.log("-----slugged: " + stats(g2) + ", " + (100 * g2 / g1).toFixed(2) + ' %');
    console.log("-----encoded: " + stats(g3) + ", " + (100 * g3 / g2).toFixed(2) + ' %');
    console.log('ratio: ' + (100 * g3 / start).toFixed(2) + ' %');
    console.log("BEST ratio: " + stats(best.length) + ", " + (100 * best.length / start).toFixed(2) + ' %');
    return best;
};
var saveToLocalStorage = function (frames, animationName) { return window.localStorage.setItem(animationName, serializeFrames(frames)); };
var serializer = {
    saveToLocalStorage: saveToLocalStorage,
};
export default serializer;
