import constants from "../constants";
var Elements = (function () {
    function Elements() {
        var _this = this;
        this.setupForEditorMode = function (g) {
            _this.scrollContainer.style.pointerEvents = 'none';
            _this.outerEditor.style.display = 'block';
            _this.body.style.backgroundColor = "#FFF";
        };
        this.setupForNormalMode = function (g) {
            _this.scrollContainer.style.pointerEvents = 'auto';
            _this.outerEditor.style.display = 'none';
            _this.body.style.backgroundColor = constants.colors[0];
        };
        this.body = document.body;
        this.outer = document.getElementById("outer");
        this.svgOuter = document.getElementById("svg");
        this.rootSvgGroup = document.getElementById("rootGroup");
        this.frameTiming = document.getElementById("editor-frame-timing");
        this.scrollContainer = document.getElementById('scrollOuter');
        this.outerEditor = document.getElementById('editor-outer');
        this.outerColors = document.getElementById('editor-colors');
        this.inputWait = document.getElementById('editor-option-wait');
        this.inputFade = document.getElementById('editor-option-fade');
        this.inputSpeed = document.getElementById('editor-option-speed');
        this.inputAnimationName = document.getElementById('editor-option-name');
        this.inputGridName = document.getElementById('editor-option-grid-name');
        this.inputTiming = document.getElementById('editor-option-timing');
        this.inputCursor = document.getElementById('editor-option-cursor-mode');
        this.useTGroups = document.getElementById('editor-use-tgroups');
        this.btnUndo = document.getElementById('editor-option-undo');
        this.btnFrame = document.getElementById('editor-option-frame');
        this.btnPlay = document.getElementById('editor-option-play');
        this.btnSave = document.getElementById('editor-option-save');
        this.btnLoad = document.getElementById('editor-option-load');
        this.btnAddAnimation = document.getElementById('editor-option-add-animation');
        this.btnRecolor = document.getElementById('editor-option-recolor');
        this.btnRandom = document.getElementById('editor-option-random');
        this.btnSaveGrid = document.getElementById('editor-option-save-grid');
        this.btnLoadGrid = document.getElementById('editor-option-load-grid');
        this.btnShowFrameTiming = document.getElementById('editor-toggle-frame-timing');
        this.divCurrentTime = document.getElementById('editor-current-time');
        this.colorButtons = [
            document.getElementById('btnColor_0'),
            document.getElementById('btnColor_1'),
            document.getElementById('btnColor_2'),
            document.getElementById('btnColor_3'),
            document.getElementById('btnColor_4'),
            document.getElementById('btnColor_5'),
            document.getElementById('btnColor_6'),
            document.getElementById('btnColor_7'),
            document.getElementById('btnColor_8'),
            document.getElementById('btnColor_9')
        ];
        this.inputs = [
            this.inputWait,
            this.inputFade,
            this.inputSpeed,
            this.inputAnimationName,
            this.inputGridName,
        ];
    }
    return Elements;
}());
export default Elements;
