var getArrayDeltaFromLeft = function (prev, cur) {
    var result = [];
    var changeFound = false;
    for (var i = cur.length - 1; i >= 0; i--) {
        if (changeFound) {
            result = [cur[i]].concat(result);
        }
        else if (cur[i] !== prev[i]) {
            result = [cur[i]].concat(result);
            changeFound = true;
        }
    }
    return result;
};
export var compressArrayArrayToDeltasFromLeft = function (lists) {
    if (lists.length === 0) {
        return lists;
    }
    var compressed = [lists[0].slice()];
    lists.slice(1).reduce(function (prev, cur) {
        compressed.push(getArrayDeltaFromLeft(prev, cur));
        return cur;
    }, lists[0].slice());
    return compressed;
};
export var decompressDeltasToArrayArrayFromLeft = function (lists) {
    if (lists.length === 0) {
        return lists;
    }
    var uncompressed = [lists[0]];
    var state = Array.from(lists[0]);
    lists.slice(1).forEach(function (s) {
        var newList = Array.from(s);
        state.forEach(function (stateVal, i) {
            if (newList[i] === undefined) {
                newList.push(stateVal);
            }
            else {
                state[i] = newList[i];
            }
        });
        uncompressed.push(newList);
    });
    return uncompressed;
};
