var encodings = [];
var c = 65;
var getNextChar = function () {
    if (c === 91) {
        c = 97;
    }
    return String.fromCharCode(c++);
};
[24, 16, 8, 4, 3, 2].forEach(function (n) {
    ['0', '1', '2', '3', '4', '5'].forEach(function (colorIdx) {
        encodings.push({
            match: colorIdx.repeat(n),
            code: getNextChar(),
        });
    });
});
export var addEncodings = function (s) { return encodings.reduce(function (prev, cur) { return prev.replace(new RegExp(cur.match, 'g'), cur.code); }, s); };
export var removeEncodings = function (s) { return encodings.reduce(function (prev, cur) { return prev.replace(new RegExp(cur.code, 'g'), cur.match); }, s); };
