import constants, { getCenterX, getCenterY } from "../constants";
var maxCols = constants.maxCols, maxRows = constants.maxRows;
var getZeroOffset = function () { return 0; };
var getSwipeRight = function (dur) { return function (row, col) { return (col / maxCols) * dur; }; };
var getSwipeLeft = function (dur) { return function (row, col) { return ((maxCols - col) / maxCols) * dur; }; };
var swipeRightSlow = getSwipeRight(1);
var swipeRight = getSwipeRight(0.5);
var swipeRightFast = getSwipeRight(0.2);
var swipeLeftSlow = getSwipeLeft(1);
var swipeLeft = getSwipeLeft(0.5);
var swipeLeftFast = getSwipeLeft(0.2);
var scaleAll = 3;
var diag1 = function (row, col) {
    return ((((maxRows - row) + col) / (maxRows + maxCols)) * 2) * scaleAll;
};
var diag2 = function (row, col) { return (((row + col) % 2)
    ? (((maxRows - row) + col) / (maxRows + maxCols)) * 0.3
    : (((maxRows - row) + col) / (maxRows + maxCols)) * 0.3 + 0.05) * scaleAll; };
var diag3 = function (row, col) {
    return ((((maxRows - row) * 2 + (maxCols - col)) / (maxRows + maxCols)) * 0.3) * scaleAll;
};
var diag4 = function (row, col) { return (((row + col) % 2)
    ? (((maxRows - row) * 2 + col) / (maxRows * 2 + maxCols)) * 0.3
    : (((maxRows - row) * 2 + col) / (maxRows * 2 + maxCols)) * 0.3 + 0.05) * scaleAll; };
var randomFade = function () { return Math.random() * 1; };
var randomSwipeUp = function (row, col) { return ((maxRows - row) / maxRows) * 0.7 + randomFade() * 0.3; };
var randomSwipeUp2 = function (row, col) { return ((maxRows - row) / maxRows) * 0.4 + randomFade() * 0.6; };
var center = function (row, col) {
    var dist = Math.sqrt(Math.pow(getCenterX() - col, 2) + Math.pow(getCenterY() - row, 2) * 0.33);
    return dist / 20;
};
var thingRows = [
    [2, 17, 10, 1],
    [3, 18, 9, 0],
    [4, 19, 8, 5],
    [21, 20, 7, 6],
    [22, 13, 14, 23],
    [11, 12, 15, 16],
    [10, 1, 2, 17],
    [9, 0, 3, 18],
    [8, 5, 4, 19],
    [7, 6, 21, 20],
    [14, 23, 22, 13],
    [15, 16, 11, 12],
];
var thing = function (row, col) { return thingRows[row % 12][col % 4] * 0.25; };
export var getTimingFunction = function (name) {
    switch (name) {
        case 'swipe_left_slow':
            return swipeLeftSlow;
        case 'swipe_left':
            return swipeLeft;
        case 'swipe_left_fast':
            return swipeLeftFast;
        case 'swipe_right_slow':
            return swipeRightSlow;
        case 'swipe_right':
            return swipeRight;
        case 'swipe_right_fast':
            return swipeRightFast;
        case 'diag_1':
            return diag1;
        case 'diag_2':
            return diag2;
        case 'diag_3':
            return diag3;
        case 'diag_4':
            return diag4;
        case 'rand':
            return randomFade;
        case 'rand_swipe':
            return randomSwipeUp;
        case 'rand_swipe_2':
            return randomSwipeUp2;
        case 'center':
            return center;
        case 'thing':
            return thing;
        default:
            return getZeroOffset;
    }
};
export var getTimingFunctionNameFromCode = function (code) {
    switch (code) {
        case '':
            return 'none';
        case '1':
            return 'swipe_right_slow';
        case '2':
            return 'swipe_right';
        case '3':
            return 'swipe_right_fast';
        case '4':
            return 'swipe_left_slow';
        case '5':
            return 'swipe_left';
        case '6':
            return 'swipe_left_fast';
        case '7':
            return 'diag_1';
        case '8':
            return 'diag_2';
        case '9':
            return 'diag_3';
        case '0':
            return 'diag_4';
        case '{':
            return 'rand';
        case '}':
            return 'rand_swipe';
        case '?':
            return 'rand_swipe_2';
        case '!':
            return 'center';
        case '[':
            return 'thing';
        default:
            return 'none';
    }
};
export var getTimingFunctionCodeSlug = function (name) {
    switch (name) {
        case 'swipe_right_slow':
            return ',1';
        case 'swipe_right':
            return ',2';
        case 'swipe_right_fast':
            return ',3';
        case 'swipe_left_slow':
            return ',4';
        case 'swipe_left':
            return ',5';
        case 'swipe_left_fast':
            return ',6';
        case 'diag_1':
            return ',7';
        case 'diag_2':
            return ',8';
        case 'diag_3':
            return ',9';
        case 'diag_4':
            return ',0';
        case 'rand':
            return ',{';
        case 'rand_swipe':
            return ',}';
        case 'rand_swipe_2':
            return ',?';
        case 'center':
            return ',!';
        case 'thing':
            return ',[';
        default:
            return '';
    }
};
